* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}


body {
    height: 100vh;
    width: 100vw;

    display: grid;
    grid-template-columns: clamp(200px, 20%, 300px) 4fr;
    grid-template-rows: 1fr;
}

#editProjectName {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

#editProjectName > form {
    display: flex;
    flex-direction: column;

    padding: 20px;
    gap: 10px;

    font-size: 2rem;
}

.dialogClose {
    align-self: flex-end;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    height: 30px;
    width: 30px;
    border: none;
}

.dialogClose:hover {
    box-shadow: 0px 0px 0px 1px rgb(0, 0, 0) inset;
    cursor: pointer;
}

.dialogSubmit {
    align-self: center;
    font-size: 1rem;
    width: 50%;
}

.entryPair > input {
    font-size: 1.75rem;
}

#projectSection,
#toDoSection, 
#projectContainer,
#toDoContainer {
    display: flex;
    flex-direction: column;
}

#projectSection {
    background-color: rgb(230, 230, 230);
    padding: 20px;
    gap: 20px;
}

#toDoSection {
    border: 1px solid rgb(255, 0, 0);
}

h1 {
    text-align: center;
}

#projectHeader {
    font-size: 2rem;
    padding: 25px;
}

#toDoHeader {
    font-size: 3rem;
    padding-top: 100px;
    padding-bottom: 50px;
}

#projectContainer {
    gap: 10px;
}

#toDoContainer {
    padding: 0px clamp(75px, 20%, 150px);
}

.projectItem {
    display: flex;
    flex-direction: row;

    background-color: rgb(255, 255, 255);
    border-radius: 10px;

    padding: 10px;
}

.projectItem:hover,
#active.projectItem,
#projectSection > .addItem:hover {
    background-color: rgb(165, 165, 165);
}

.projectItem:active,
#projectSection > .addItem:active {
    box-shadow: 0px 0px 0px 1px rgb(0, 0, 0) inset;
}

.projectItem > p {
    margin-right: auto;
}

.projectItem > button {
    border: none;
    height: 20px;
    width: 20px;
    opacity: 0;
    visibility: hidden;
}

.projectItem:hover > button,
#active.projectItem > button {
    opacity: 1;
    visibility: visible;
}

.projectItem > button:hover {
    box-shadow: 0px 0px 0px 1px rgb(0, 0, 0) inset;
    cursor: pointer;
}

.toDoItem {
    border: 1px solid rgb(255, 0, 0);
}

.editBtn {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.removeBtn {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.addItem {
    padding: 10px;
    border: none;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
}

